import React, { useState, useEffect } from 'react';
import AdminNavbar from '../components/AdminNavbar';
import { FaEye, FaTrash, FaSearch, FaCheck, FaBan } from 'react-icons/fa';
import { toast } from 'react-toastify';
import LoadingSpinner from '../components/LoadingSpinner';
import orderApi from '../api/orderApi';

const ManageOrders = () => {
    const [orders, setOrders] = useState([]);
    const [loading, setLoading] = useState(false);
    const [searchTerm, setSearchTerm] = useState('');
    const [showDetail, setShowDetail] = useState(false);
    const [orderDetails, setOrderDetails] = useState(null);

    useEffect(() => {
        fetchOrders();
    }, []);

    const fetchOrders = async () => {
        try {
            setLoading(true);
            const response = await orderApi.getAllOrders();
            if (response.status === 'OK') {
                const formattedOrders = response.data.map(order => ({
                    _id: order._id,
                    uId: order.uId,
                    oOrderDate: order.createdAt,
                    oTotalAmount: order.oTotalPrice,
                    status: order.oStatus,
                    oShippingAddress: order.oShippingAddress,
                    oPaymentMethod: order.oPaymentMethod,
                }));
                setOrders(formattedOrders);
            } else {
                toast.error('Li khi ti danh sch n hng');
            }
        } catch (error) {
            console.error('Error:', error);
            toast.error('Khng th kt ni n server');
        } finally {
            setLoading(false);
        }
    };

    const handleConfirm = async (id) => {
        if (window.confirm('Bn c chc chn mun xc nhn n hng ny?')) {
            try {
                setLoading(true);
                const response = await orderApi.confirmOrder(id);
                if (response.status === 'OK') {
                    toast.success('Xc nhn n hng thnh cng');
                    fetchOrders();
                } else {
                    toast.error(response.message);
                }
            } catch (error) {
                toast.error('C li xy ra khi xc nhn n hng');
            } finally {
                setLoading(false);
            }
        }
    };

    const handleDelete = async (id) => {
        if (window.confirm('Bn c chc chn mun xa n hng ny?')) {
            try {
                setLoading(true);
                const response = await orderApi.deleteOrder(id);
                if (response.status === 'OK') {
                    toast.success('Xa n hng thnh cng');
                    fetchOrders();
                } else {
                    toast.error(response.message);
                }
            } catch (error) {
                toast.error('C li xy ra khi xa n hng');
            } finally {
                setLoading(false);
            }
        }
    };

    const handleCancel = async (id) => {
        if (window.confirm('Bn c chc chn mun hy n hng ny?')) {
            try {
                setLoading(true);
                const response = await orderApi.cancelOrder(id);
                if (response.status === 'OK') {
                    toast.success('Hy n hng thnh cng');
                    setOrders(prevOrders => prevOrders.map(order =>
                        order._id === id ? { ...order, status: 'CANCELLED' } : order
                    ));
                } else {
                    toast.error(response.message);
                }
            } catch (error) {
                toast.error('C li xy ra khi hy n hng');
            } finally {
                setLoading(false);
            }
        }
    };

    const fetchOrderDetails = async (id) => {
        try {
            setLoading(true);
            const response = await orderApi.getDetailOrder(id);
            if (response.status === 'OK') {
                setOrderDetails(response.data);
                setShowDetail(true);
            } else {
                toast.error('Li khi ti chi tit n hng');
            }
        } catch (error) {
            console.error('Error:', error);
            toast.error('Khng th kt ni n server');
        } finally {
            setLoading(false);
        }
    };

    const filteredOrders = orders.filter(order =>
        order._id.toLowerCase().includes(searchTerm.toLowerCase()) ||
        order.uId.toLowerCase().includes(searchTerm.toLowerCase())
    );

    return (
        <div className="min-h-screen bg-gray-100">
            <AdminNavbar />
            <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
                <div className="mb-6">
                    <h1 className="text-3xl font-bold text-gray-900">Qun l n hng</h1>
                    <p className="mt-2 text-sm text-gray-600">Qun l v cp nht thng tin n hng</p>
                </div>
                <div className="flex flex-col sm:flex-row justify-between items-center mb-6 gap-4">
                    <div className="relative w-full sm:w-96">
                        <input
                            type="text"
                            placeholder="Tm kim n hng..."
                            className="w-full pl-10 pr-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:border-indigo-500"
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                        />
                        <FaSearch className="absolute right-3 top-3 text-gray-400" />
                    </div>
                </div>
                <div className="bg-white shadow-md rounded-lg overflow-hidden">
                    {loading ? (
                        <LoadingSpinner />
                    ) : (
                        <div className="overflow-x-auto">
                            <table className="min-w-full divide-y divide-gray-200">
                                <thead className="bg-gray-50">
                                    <tr>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">M n</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Phng thc thanh ton</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Ngy t</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Tng tin</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Trng thi</th>
                                        <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase">Thao tc</th>
                                    </tr>
                                </thead>
                                <tbody className="bg-white divide-y divide-gray-200">
                                    {filteredOrders.length === 0 ? (
                                        <tr>
                                            <td colSpan="6" className="px-6 py-4 text-center text-gray-500">
                                                Khng c n hng no
                                            </td>
                                        </tr>
                                    ) : (
                                        filteredOrders.map((order) => (
                                            <tr key={order._id} className={`hover:bg-gray-100 transition duration-150 ease-in-out ${order.status === 'CONFIRMED' ? 'bg-green-50' :
                                                order.status === 'CANCELLED' ? 'bg-red-50' : ''
                                                }`}>
                                                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                                                    {order._id}
                                                </td>
                                                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                                    {order.oPaymentMethod === 'COD' ? 'Thanh ton khi nhn hng' : 'Thanh ton qua PayPal'}
                                                </td>
                                                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                                    {new Date(order.oOrderDate).toLocaleDateString('vi-VN')}
                                                </td>
                                                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                                    {order.oTotalAmount ? `${parseInt(order.oTotalAmount).toLocaleString('vi-VN')}` : '0'}
                                                </td>
                                                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                                    {order.status === 'CONFIRMED' ? ' xc nhn' :
                                                        order.status === 'CANCELLED' ? ' hy' : 'Ch xc nhn'}
                                                </td>
                                                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium space-x-3">
                                                    <button
                                                        onClick={() => fetchOrderDetails(order._id)}
                                                        className="text-blue-600 hover:text-blue-900"
                                                        title="Xem chi tit"
                                                    >
                                                        <FaEye className="inline-block w-4 h-4" />
                                                    </button>
                                                    <button
                                                        onClick={() => handleConfirm(order._id)}
                                                        className="text-green-600 hover:text-green-900"
                                                        title="Xc nhn"
                                                    >
                                                        <FaCheck className="inline-block w-4 h-4" />
                                                    </button>
                                                    <button
                                                        onClick={() => handleCancel(order._id)}
                                                        className="text-yellow-600 hover:text-yellow-900"
                                                        title="Hy"
                                                    >
                                                        <FaBan className="inline-block w-4 h-4" />
                                                    </button>
                                                    <button
                                                        onClick={() => handleDelete(order._id)}
                                                        className="text-red-600 hover:text-red-900"
                                                        title="Xa"
                                                    >
                                                        <FaTrash className="inline-block w-4 h-4" />
                                                    </button>
                                                </td>
                                            </tr>
                                        ))
                                    )}
                                </tbody>
                            </table>
                        </div>
                    )}
                </div>
                {showDetail && orderDetails && (
                    <div className="mt-6 bg-white shadow-md rounded-lg p-6">
                        <h2 className="text-xl font-bold text-gray-800 mb-4">Chi tit n hng</h2>
                        <p><strong>M n hng:</strong> {orderDetails._id}</p>
                        <p><strong>Khch hng:</strong> {orderDetails.uId}</p>
                        <p><strong>a ch giao hng:</strong> {orderDetails.oShippingAddress}</p>
                        <p><strong>Phng thc thanh ton:</strong> {orderDetails.oPaymentMethod === 'COD' ? 'Thanh ton khi nhn hng' : 'Thanh ton qua PayPal'}</p>
                        <p><strong>Tng tin:</strong> {orderDetails.oTotalPrice.toLocaleString('vi-VN')}</p>
                        <p><strong>Trng thi:</strong> {orderDetails.oStatus}</p>
                        <h3 className="text-lg font-semibold text-gray-800 mt-4">Sn phm:</h3>
                        <ul className="list-disc pl-5">
                            {orderDetails.oItems.map(item => (
                                <li key={item.prId}>
                                    <p><strong>Sn phm ID:</strong> {item.prId}</p>
                                    <p><strong>S lng:</strong> {item.prQuantity}</p>
                                    <p><strong>Gi:</strong> {item.prPrice.toLocaleString('vi-VN')}</p>
                                </li>
                            ))}
                        </ul>
                        <button
                            onClick={() => setShowDetail(false)}
                            className="mt-4 text-blue-600 hover:text-blue-900"
                        >
                            ng
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
};

export default ManageOrders;