import React from 'react';
import { Link } from 'react-router-dom';
import { FaPaypal, FaCcVisa, FaCcMastercard, FaMoneyBillWave } from 'react-icons/fa';

const footerData = {
    sections: [
        {
            title: 'H tr khch hng',
            links: [
                { label: 'Hotline: 1900-xxxx', path: '#' },
                { label: 'Hng dn t hng', path: '#' },
                { label: 'Phng thc vn chuyn', path: '#' },
                { label: 'Chnh sch i tr', path: '#' }
            ]
        },
        {
            title: 'V The Book Loft',
            links: [
                { label: 'Gii thiu', path: '#' },
                { label: 'iu khon s dng', path: '#' },
                { label: 'Chnh sch bo mt', path: '#' },
                { label: 'Tuyn dng', path: '#' }
            ]
        },
        {
            title: 'Hp tc v lin kt',
            links: [
                { label: 'Quy ch hot ng', path: '#' },
                { label: 'Bn hng cng chng ti', path: '#' }
            ]
        }
    ],
    payments: [
        { icon: FaPaypal, label: 'PayPal' },
        { icon: FaCcVisa, label: 'Visa' },
        { icon: FaCcMastercard, label: 'Mastercard' },
        { icon: FaMoneyBillWave, label: 'Tin mt' }
    ]
};

const Footer = () => {
    return (
        <footer className="bg-gradient-to-r from-blue-500 to-purple-300 text-black">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
                    {footerData.sections.map((section, index) => (
                        <div key={index}>
                            <h3 className="text-lg font-semibold mb-4">
                                {section.title}
                            </h3>
                            <ul className="space-y-3">
                                {section.links.map((link, idx) => (
                                    <li key={idx}>
                                        <Link
                                            to={link.path}
                                            className="hover:text-blue-400 transition duration-200"
                                        >
                                            {link.label}
                                        </Link>
                                    </li>
                                ))}
                            </ul>
                        </div>
                    ))}

                    <div>
                        <h3 className="text-lg font-semibold mb-4">Phng thc thanh ton</h3>
                        <div className="flex flex-wrap gap-4">
                            {footerData.payments.map((payment, index) => (
                                <payment.icon
                                    key={index}
                                    className="text-black hover:text-blue-400 transition duration-200"
                                    size={24}
                                    title={payment.label}
                                />
                            ))}
                        </div>
                    </div>
                </div>
            </div>

            <div className="bg-gradient-to-r from-blue-500 to-purple-300 border-t border-gray-700 mt-auto">
                <div className="max-w-7xl mx-auto px-4 py-4 sm:py-6">
                    <div className="flex flex-col sm:flex-row justify-between items-center space-y-4 sm:space-y-0">
                        <p className="text-sm"> {new Date().getFullYear()} The Book Loft.  ng k bn quyn.</p>
                        <div className="flex space-x-4 sm:space-x-6">
                            <Link to="#" className="text-sm hover:text-blue-400 transition-colors">iu khon</Link>
                            <Link to="#" className="text-sm hover:text-blue-400 transition-colors">Chnh sch</Link>
                            <Link to="#" className="text-sm hover:text-blue-400 transition-colors">Lin h</Link>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    );
};

export default Footer;