import React, { useState, useEffect } from 'react';
import AdminNavbar from '../components/AdminNavbar';
import { FaPlus, FaEdit, FaTrash, FaSearch } from 'react-icons/fa';
import { toast } from 'react-toastify';
import LoadingSpinner from '../components/LoadingSpinner';
import productApi from '../api/productApi';
import InputField from '../components/InputField';
const categories = [
    'Sch vn hc',
    'Sch khoa hc',
    'Sch kinh t',
    'Tiu thuyt',
    'Truyn tranh',
    'Sch ngoi ng',
    'Sch thiu nhi'
];

const ManageProducts = () => {
    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(false);
    const [showForm, setShowForm] = useState(false);
    const [editingProduct, setEditingProduct] = useState(null);
    const [searchTerm, setSearchTerm] = useState('');
    const [formData, setFormData] = useState({
        prTitle: '',
        prAuthor: '',
        prCategory: '',
        prDescription: '',
        prImage: '',
        prStockQuantity: '',
        prPrice: ''
    });

    useEffect(() => {
        fetchProducts();
    }, []);

    const fetchProducts = async () => {
        try {
            setLoading(true);
            const response = await productApi.getAllProducts();
            if (response.status === 'OK') {
                setProducts(response.data);
            }
        } catch (error) {
            console.error('Li khi ly danh sch sn phm:', error);
            toast.error('Khng th ti danh sch sn phm');
        } finally {
            setLoading(false);
        }
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
    };

    const validateForm = () => {
        if (!formData.prTitle.trim()) return 'Vui lng nhp tn sn phm';
        if (!formData.prAuthor.trim()) return 'Vui lng nhp tn tc gi';
        if (!formData.prCategory) return 'Vui lng chn danh mc';
        if (!formData.prImage.trim()) return 'Vui lng nhp URL hnh nh';
        if (!formData.prPrice || formData.prPrice <= 0) return 'Vui lng nhp gi hp l';
        if (!formData.prStockQuantity || formData.prStockQuantity < 0) return 'Vui lng nhp s lng hp l';
        return null;
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        const error = validateForm();
        if (error) {
            toast.error(error);
            return;
        }

        try {
            setLoading(true);
            const response = editingProduct
                ? await productApi.updateProduct(editingProduct._id, formData)
                : await productApi.createProduct(formData);

            if (response.status === 'OK') {
                toast.success(editingProduct ? 'Cp nht thnh cng' : 'Thm mi thnh cng');
                setShowForm(false);
                resetForm();
                fetchProducts();
            } else {
                toast.error(response.data.message);
            }
        } catch (error) {
            toast.error('C li xy ra khi x l yu cu');
        } finally {
            setLoading(false);
        }
    };

    const handleDelete = async (id) => {
        if (window.confirm('Bn c chc chn mun xa sn phm ny?')) {
            try {
                setLoading(true);
                const response = await productApi.deleteProduct(id);
                if (response.status === 'OK') {
                    toast.success('Xa sn phm thnh cng');
                    fetchProducts();
                } else {
                    toast.error(response.data.message);
                }
            } catch (error) {
                toast.error('C li xy ra khi xa sn phm');
            } finally {
                setLoading(false);
            }
        }
    };

    const handleEdit = (product) => {
        setEditingProduct(product);
        setFormData({
            prTitle: product.prTitle,
            prAuthor: product.prAuthor,
            prCategory: product.prCategory,
            prDescription: product.prDescription || '',
            prImage: product.prImage,
            prStockQuantity: product.prStockQuantity,
            prPrice: product.prPrice
        });
        setShowForm(true);
    };

    const resetForm = () => {
        setFormData({
            prTitle: '',
            prAuthor: '',
            prCategory: '',
            prDescription: '',
            prImage: '',
            prStockQuantity: '',
            prPrice: ''
        });
        setEditingProduct(null);
    };

    const filteredProducts = products.filter(product =>
        product.prTitle.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.prAuthor.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.prCategory.toLowerCase().includes(searchTerm.toLowerCase())
    );

    return (
        <div className="min-h-screen bg-gray-100">
            <AdminNavbar />
            <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
                <div className="mb-6">
                    <h1 className="text-3xl font-bold text-gray-900">Qun l sn phm</h1>
                    <p className="mt-2 text-sm text-gray-600">Qun l v cp nht thng tin sn phm</p>
                </div>
                <div className="flex flex-col sm:flex-row justify-between items-center mb-6 gap-4">
                    <div className="relative w-full sm:w-96">
                        <input
                            type="text"
                            placeholder="Tm kim sn phm..."
                            className="w-full pl-10 pr-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:border-indigo-500"
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                        />
                        <FaSearch className="absolute right-3 top-3 text-gray-400" />
                    </div>
                    <button
                        onClick={() => {
                            resetForm();
                            setShowForm(true);
                        }}
                        className="w-full sm:w-auto bg-indigo-600 text-white px-6 py-2 rounded-lg hover:bg-indigo-700 transition-colors flex items-center justify-center gap-2"
                    >
                        <FaPlus />
                    </button>
                </div>
                <div className="bg-white shadow-md rounded-lg overflow-hidden">
                    {loading ? (
                        <LoadingSpinner />
                    ) : (
                        <div className="overflow-x-auto">
                            <table className="min-w-full divide-y divide-gray-200">
                                <thead className="bg-gray-50">
                                    <tr>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase w-2/12">Sn phm</th>
                                        <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase w-2/12">Tc gi</th>
                                        <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase w-2/12">Danh mc</th>
                                        <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase w-2/12">Gi</th>
                                        <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase w-2/12">S lng</th>
                                        <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase w-2/12">Thao tc</th>
                                    </tr>
                                </thead>
                                <tbody className="bg-white divide-y divide-gray-200">
                                    {filteredProducts.length === 0 ? (
                                        <tr>
                                            <td colSpan="6" className="px-6 py-4 text-center text-gray-500">
                                                Khng c sn phm no
                                            </td>
                                        </tr>
                                    ) : (
                                        filteredProducts.map((product) => (
                                            <tr key={product._id} className="hover:bg-gray-100 transition duration-150 ease-in-out">
                                                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{product.prTitle}</td>
                                                <td className="px-6 py-4 whitespace-nowrap text-center text-sm text-gray-500">{product.prAuthor}</td>
                                                <td className="px-6 py-4 whitespace-nowrap text-center">
                                                    <span className="px-2 py-1 text-xs font-semibold rounded-full bg-green-100 text-green-800">{product.prCategory}</span>
                                                </td>
                                                <td className="px-6 py-4 whitespace-nowrap text-center text-sm text-gray-900">
                                                    {new Intl.NumberFormat('vi-VN', {
                                                        style: 'currency',
                                                        currency: 'VND'
                                                    }).format(product.prPrice)}
                                                </td>
                                                <td className="px-6 py-4 whitespace-nowrap text-center text-sm text-gray-900">
                                                    <span className={`${product.prStockQuantity <= 10 ? 'text-red-600' : 'text-gray-900'}`}>
                                                        {product.prStockQuantity}
                                                    </span>
                                                </td>
                                                <td className="px-6 py-4 whitespace-nowrap text-right space-x-2">
                                                    <button
                                                        onClick={() => handleEdit(product)}
                                                        className="text-indigo-600 hover:text-indigo-900 transition duration-150 ease-in-out"
                                                        title="Sa"
                                                    >
                                                        <FaEdit className="inline-block w-4 h-4" />
                                                    </button>
                                                    <button
                                                        onClick={() => handleDelete(product._id)}
                                                        className="text-red-600 hover:text-red-900 transition duration-150 ease-in-out"
                                                        title="Xa"
                                                    >
                                                        <FaTrash className="inline-block w-4 h-4" />
                                                    </button>
                                                </td>
                                            </tr>
                                        ))
                                    )}
                                </tbody>
                            </table>
                        </div>
                    )}
                </div>
                {showForm && (
                    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
                        <div className="bg-white rounded-lg w-full max-w-2xl">
                            <div className="p-6">
                                <h2 className="text-2xl font-bold mb-4">
                                    {editingProduct ? 'Cp nht sn phm' : 'Thm sn phm mi'}
                                </h2>
                                <form onSubmit={handleSubmit} className="space-y-4">
                                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                        <InputField
                                            label="Tn sch"
                                            name="prTitle"
                                            value={formData.prTitle}
                                            onChange={handleInputChange}
                                            required
                                        />
                                        <InputField
                                            label="Tc gi"
                                            name="prAuthor"
                                            value={formData.prAuthor}
                                            onChange={handleInputChange}
                                            required
                                        />
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700">Danh mc</label>
                                            <select
                                                name="prCategory"
                                                value={formData.prCategory}
                                                onChange={handleInputChange}
                                                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                                                required
                                            >
                                                <option value="">Chn danh mc</option>
                                                {categories.map(category => (
                                                    <option key={category} value={category}>{category}</option>
                                                ))}
                                            </select>
                                        </div>
                                        <InputField
                                            label="URL Hnh nh"
                                            name="prImage"
                                            value={formData.prImage}
                                            onChange={handleInputChange}
                                            required
                                        />
                                        <InputField
                                            label="Gi"
                                            name="prPrice"
                                            type="number"
                                            value={formData.prPrice}
                                            onChange={handleInputChange}
                                            required
                                        />
                                        <InputField
                                            label="S lng"
                                            name="prStockQuantity"
                                            type="number"
                                            value={formData.prStockQuantity}
                                            onChange={handleInputChange}
                                            required
                                        />
                                    </div>
                                    <div>
                                        <label className="block text-sm font-medium text-gray-700">M t</label>
                                        <textarea
                                            name="prDescription"
                                            value={formData.prDescription}
                                            onChange={handleInputChange}
                                            rows="3"
                                            className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                                        />
                                    </div>
                                    <div className="flex justify-end gap-4 mt-6">
                                        <button
                                            type="button"
                                            onClick={() => {
                                                setShowForm(false);
                                                resetForm();
                                            }}
                                            className="bg-gray-500 text-white px-4 py-2 rounded-lg hover:bg-gray-600 transition-colors"
                                        >
                                            Hy
                                        </button>
                                        <button
                                            type="submit"
                                            className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 transition-colors"
                                        >
                                            {editingProduct ? 'Cp nht' : 'Thm mi'}
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

export default ManageProducts;